.pagination {
  display: inline-flex;
  margin-top: rem(30);
  a{
    color: #afafaf;
    padding: rem(8) rem(16);
    text-decoration: none;
    transition: background-color .3s;
    border: 1px solid #ddd;
    font-size: rem(13);
    &:hover:not(.active){
      color: #fff;
      background: darken(#afafaf, 5%);
    }
  }
  a.active {
    background-color: #afafaf;
    color: white;
    border: 1px solid #afafaf;
  }
}
